.icon {
	.icon-shape (@size-icon, @padding-icon, @border-radius-icon);
	.icon-gradient (@icon-default-color-top, @icon-default-color-bottom);

// padding suprascris pentru iconite 7s si fa, test!
    padding: 8px;
    
	[class^="icon-"]{
		height: @height-icon-message;
		width:  @width-icon-message;
		background-repeat: no-repeat;
		background-size: 100%;
	}	

	&.icon-sm {
		.icon-shape (@size-icon-sm, @padding-icon-sm, @border-radius-icon-sm);

		[class^="icon-"]{
			height: @height-icon-message-sm;
			width:  @width-icon-message-sm;
							
		}
		
		[class*="pe-7s-"]{
        	width: 25px;
        	height: 25px;
        	line-height: 25px;
        	font-size: 25px;
        	text-align: center;
        	color: #FFFFFF;
    	}
    	[class*="fa-"]{
        	width: 25px;
        	height: 25px;
        	line-height: 25px;
        	font-size: 19px;
        	text-align: center;
        	color: #FFFFFF;
    	}
	}

	[class*="pe-7s-"]{
    	width: 50px;
    	height: 50px;
    	line-height: 50px;
    	font-size: 46px;
    	text-align: center;
    	color: #FFFFFF;
	}
	[class*="fa-"]{
    	width: 50px;
    	height: 50px;
    	line-height: 50px;
    	font-size: 36px;
    	text-align: center;
    	color: #FFFFFF;
	}

 }

