.info{
    margin-top: 20px;
    margin-bottom: 30px;
    text-align: center;
    .icon{
        margin-top: 0;
    }
    h4,
    .h4{
        margin-top: 20px;
        margin-bottom: 5px;   
    }
}

.info-horizontal{
    text-align: left;
    margin-top: 0;
    .icon{
        float: left;
        margin-top: 20px;
        margin-right: 20px;
    }
    .description{
        overflow: hidden;
    }
}









